.wrapper {
    box-sizing: border-box;
    width: 100%;
    max-width: 600px;
}

.ingredients {
    max-height: calc(100vh - 300px);
    height: 100%;
    overflow-y: scroll;
    scrollbar-width: thin;
}

.ingredients::-webkit-scrollbar {
    width: 5px;
    height: 8px;
    background-color: #2f2f37;
}

.ingredients::-webkit-scrollbar-thumb {
    background: #8585AD;
}