.link {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 8px;
    padding: 16px 20px;
    text-decoration: none;
    transition: all 0.1s ease-in;
}

.activeLink {
    color: white;
    transition: all 0.1s ease-in;
}

.activeLink svg {
    fill: #ffffff;
}